import "./Startbtn.css";
import React from "react";
import { Link } from "react-router-dom";
import axios from "axios";

function Startbtn({ title, date, food, clock }) {
  const Startpage = [{ name: "profile", path: "/Profile" }];

  console.log(title, date, food, clock);

  const onClick = () => {
    const url =
      "http://ec2-3-145-100-212.us-east-2.compute.amazonaws.com:5000/add_bab";
    axios.post(url, {
      title: "임시",
      date: date,
      food: food,
      clock: clock,
    });
  };

  return (
    <div classNme="start">
      {Startpage.map((profile, index) => {
        return (
          <Link to={"./Profile"} key={index}>
            <button className="startbtn" onClick={onClick}>
              {title}
            </button>
          </Link>
        );
      })}
    </div>
  );
}

export default Startbtn;
