import react from "react";
import "./Profile.css";
import Bar from "./Bar";
import React, { useEffect, useState } from "react";
import axios from "axios";

function Profile() {
  const url =
    "http://ec2-3-145-100-212.us-east-2.compute.amazonaws.com:5000/Profile/dahye";
  const [data, setData] = useState(
    axios
      .get(url)
      .then((res) => {
        const data = res.data;
        setData(data);
      })
      .catch((Error) => {
        console.log(Error);
      })
  );

  useEffect(() => {
    axios
      .get(url)
      .then((res) => {
        const data = res.data;
        setData(data);
      })
      .catch((Error) => {
        console.log(Error);
      });
  }, [window.location]);

  return (
    <div className="profile">
      <h2>프로필</h2>

      <div className="image2">
        <img className="image" alt="logo" src="logo/bab_small.png" />
      </div>
      <hr />

      <div className="information">
        <div className="inform">이름: {data["name"]}</div>
        <div className="inform">대학교: {data["univ"]}</div>
        <div className="inform">지역: {data["loc"]}</div>
        <div className="inform">백신접종여부: {data["vac"]}</div>
        <div className="inform">전화번호: {data["phone"]}</div>
        <div className="inform">오늘의 밥약: </div>
        <div className="inform">등록된 밥약 D-DAY</div>
      </div>
      <div className="nav-bar4">
        <Bar></Bar>
      </div>
    </div>
  );
}

export default Profile;
