import "./Itembtn.css";
import React from "react";
import { Link } from "react-router-dom";

function Itembtn({ text }) {
  const Addpage = [{ name: "item", path: "/Item" }];
  return (
    <div className="itembtn">
      {Addpage.map((item, index) => {
        return (
          <Link to={item.path} key={index}>
            <button className="itembutton">{text}</button>
          </Link>
        );
      })}
    </div>
  );
}

export default Itembtn;
