import Day from "./Day";
import "./DayList.css";
import Bar from "./Bar";
import AddButton from "./AddButton";
import React, { useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";

function DayList() {
  const [date, setDate] = useState(moment().format("YYYY-MM-DD"));
  const url =
    "http://ec2-3-145-100-212.us-east-2.compute.amazonaws.com:5000/Item/" +
    date;
  const [data, setData] = useState(
    axios
      .get(url)
      .then((res) => {
        const data = res.data;
        setData(data);
      })
      .catch((Error) => {
        console.log(Error);
      })
  );

  useEffect(() => {
    axios
      .get(url)
      .then((res) => {
        const data = res.data;
        setData(data);
      })
      .catch((Error) => {
        console.log(Error);
      });
  }, [window.location]);

  return (
    <div className="background">
      <div className="main">
        <h2>{date}의 밥팟</h2>

        <Day clock="11시" data={data}></Day>
        <Day clock="12시" data={data}></Day>
        <Day clock="13시" data={data}></Day>
        <Day clock="14시" data={data}></Day>
        <Day clock="15시" data={data}></Day>
        <Day clock="16시" data={data}></Day>
        <Day clock="17시" data={data}></Day>
        <Day clock="18시" data={data}></Day>
      </div>

      <div className="add">
        <AddButton text="Add"></AddButton>
      </div>

      <div className="nav-bar">
        <Bar></Bar>
      </div>
    </div>
  );
}

export default DayList;
